let vm = null;
const noteDatas = [
  {
    title: '我用了什麼?',
    timeStamp: 1567852748946,
    content: '<p><strong class=\"ql-size-huge\">-- 好的編輯器帶你上天堂 --</strong></p><ul><li><span class=\"ql-size-large\"> </span><a href=\"https://quilljs.com/docs/quickstart/\" target=\"_blank\" class=\"ql-size-large\">Quill</a><span class=\"ql-size-large\"> 好用耶，除非要客製化，幾乎可以隨插隨用!</span></li><li><span class=\"ql-size-large\">我是用 </span><a href=\"https://surmon-china.github.io/vue-quill-editor/\" target=\"_blank\" class=\"ql-size-large\">Vue 版</a><span class=\"ql-size-large\">插件</span></li><li><span class=\"ql-size-large\">code block 搭配 </span><a href=\"https://highlightjs.org/\" target=\"_blank\" class=\"ql-size-large\">highlight.js</a></li><li><span class=\"ql-size-large\">這次根本靠它打混掉了 XD</span></li><li><span class=\"ql-size-large\">內容是存在 localStorage，可以刪除測試看看唷!</span></li><li><span class=\"ql-size-large\">圖片可以直接插入，但會轉存 base64 喔...</span></li><li><span class=\"ql-size-large\">如果是網路圖片，可以先按右鍵複製圖片再直接貼上，就會是一般&lt;img&gt;載入</span></li></ul><p><br></p><p><span class=\"ql-size-large\">使用方式:</span></p><pre class=\"ql-syntax\" spellcheck=\"false\"><span class=\"hljs-comment\">&lt;!-- Include the Quill library --&gt;</span>\n<span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">script</span> <span class=\"hljs-attr\">src</span>=<span class=\"hljs-string\">\"https://cdn.quilljs.com/1.3.4/quill.js\"</span>&gt;&lt;/<span class=\"hljs-name\">script</span>&gt;</span>\n<span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">script</span> <span class=\"hljs-attr\">src</span>=<span class=\"hljs-string\">\"https://cdn.jsdelivr.net/npm/vue\"</span>&gt;&lt;/<span class=\"hljs-name\">script</span>&gt;</span>\n<span class=\"hljs-comment\">&lt;!-- Quill JS Vue --&gt;</span>\n<span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">script</span> <span class=\"hljs-attr\">src</span>=<span class=\"hljs-string\">\"https://cdn.jsdelivr.net/npm/vue-quill-editor@3.0.4/dist/vue-quill-editor.js\"</span>&gt;&lt;/<span class=\"hljs-name\">script</span>&gt;</span>\n<span class=\"hljs-comment\">&lt;!-- Include stylesheet --&gt;</span>\n<span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">link</span> <span class=\"hljs-attr\">href</span>=<span class=\"hljs-string\">\"https://cdn.quilljs.com/1.3.4/quill.core.css\"</span> <span class=\"hljs-attr\">rel</span>=<span class=\"hljs-string\">\"stylesheet\"</span>&gt;</span>\n<span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">link</span> <span class=\"hljs-attr\">href</span>=<span class=\"hljs-string\">\"https://cdn.quilljs.com/1.3.4/quill.snow.css\"</span> <span class=\"hljs-attr\">rel</span>=<span class=\"hljs-string\">\"stylesheet\"</span>&gt;</span>\n<span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">link</span> <span class=\"hljs-attr\">href</span>=<span class=\"hljs-string\">\"https://cdn.quilljs.com/1.3.4/quill.bubble.css\"</span> <span class=\"hljs-attr\">rel</span>=<span class=\"hljs-string\">\"stylesheet\"</span>&gt;</span>\n\n\n<span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">div</span> <span class=\"hljs-attr\">id</span>=<span class=\"hljs-string\">\"vueapp\"</span>&gt;</span>\n&nbsp; &nbsp; <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">p</span>&gt;</span>{{message}}<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">p</span>&gt;</span>\n&nbsp; &nbsp; <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">quill-editor</span> <span class=\"hljs-attr\">v-model</span>=<span class=\"hljs-string\">\"content\"</span>\n&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; <span class=\"hljs-attr\">ref</span>=<span class=\"hljs-string\">\"quillEditorA\"</span>\n&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; <span class=\"hljs-attr\">:options</span>=<span class=\"hljs-string\">\"editorOption\"</span>\n&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; @<span class=\"hljs-attr\">blur</span>=<span class=\"hljs-string\">\"onEditorBlur($event)\"</span>\n&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; @<span class=\"hljs-attr\">focus</span>=<span class=\"hljs-string\">\"onEditorFocus($event)\"</span>\n&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; @<span class=\"hljs-attr\">ready</span>=<span class=\"hljs-string\">\"onEditorReady($event)\"</span>&gt;</span>\n\t\t<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">quill-editor</span>&gt;</span>\n&nbsp; &nbsp; <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">div</span> <span class=\"hljs-attr\">class</span>=<span class=\"hljs-string\">\"content ql-editor\"</span> <span class=\"hljs-attr\">v-html</span>=<span class=\"hljs-string\">\"content\"</span>&gt;&lt;/<span class=\"hljs-name\">div</span>&gt;</span>\n<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">div</span>&gt;</span>\n</pre><p><br></p>',
    isStar: true,
  },
  {
    title: 'THE F2E 第二屆終於結束啦!!',
    timeStamp: 1567859926957,
    content: '<p><strong class=\"ql-size-huge\">(你看那夕陽多美麗啊~~</strong></p><h2><strong style=\"color: rgb(102, 163, 224);\">圖片從本地載入</strong></h2><img src=\"data:image/jpeg;base64,\"><br><br><h2><strong style=\"color: rgb(102, 163, 224);\">圖片從網路複製貼上</strong></h2><img src=\"https://imgs.gotrip.hk/wp-content/uploads/2017/08/brown-innocent_19545495235b69335e17060.jpg\">',
    isStar: false,
  }
];
$(function () {
  Vue.use(VueQuillEditor);
  vm = new Vue({
    el: "#app",
    data: {
      theme: 'light',
      noteDatas: [],
      currentNoteTimeStamp: '',
      isStarMode: false,
      newNoteTitle: '',
      editorOption: {
        theme: 'snow',
        placeholder: '來寫點甚麼吧...',
        modules: {
          toolbar: [
            ['bold', 'italic', 'underline', 'strike'],
            ['blockquote', 'code-block'],
            [{ 'header': 1 }, { 'header': 2 }],
            [{ 'list': 'ordered' }, { 'list': 'bullet' }],
            [{ 'indent': '-1' }, { 'indent': '+1' }],
            [{ 'size': ['small', false, 'large', 'huge'] }],
            [{ 'header': [1, 2, 3, 4, 5, 6, false] }],
            [{ 'color': [] }, { 'background': [] }],
            [{ 'align': [] }],
            ['link', 'image']
          ],
          syntax: {
            highlight: text => hljs.highlightAuto(text).value
          }
        },
      }
    },
    components: {
      LocalQuillEditor: VueQuillEditor.quillEditor
    },
    computed: {
      filterNotes() {
        if (this.isStarMode) {
          return this.noteDatas.filter(note => note.isStar === this.isStarMode);
        }
        return this.noteDatas;
      },
      currentNote() {
        return this.noteDatas.find(note => note.timeStamp === this.currentNoteTimeStamp);
      }
    },
    filters: {
      timeFormat(val) {
        return moment(val).fromNow();
      }
    },
    methods: {
      toggleTheme() {
        if (this.theme === 'light') {
          this.theme = 'dark';
          localStorage.setItem('noteTheme', this.theme);
          return;
        }
        this.theme = 'light';
        localStorage.setItem('noteTheme', this.theme);
      },
      onEditorBlur() {
        localStorage.setItem('noteDatas', JSON.stringify(this.noteDatas));
      },
      addNewNote() {
        if (!this.newNoteTitle.trim()) return;
        this.noteDatas.push({
          title: this.newNoteTitle,
          timeStamp: new Date().getTime(),
          content: '',
          isStar: false,
        });
        localStorage.setItem('noteDatas', JSON.stringify(this.noteDatas));
        this.newNoteTitle = '';
        this.currentNoteTimeStamp = this.noteDatas[this.noteDatas.length - 1].timeStamp;
        $('#modal').modal('hide');
      },
      switchCurrentNote(note) {
        this.currentNoteTimeStamp = note.timeStamp;
        localStorage.setItem('noteDatas', JSON.stringify(this.noteDatas));
      },
      deleteNote(timeStamp) {
        this.noteDatas = this.noteDatas.filter(note => note.timeStamp !== timeStamp);
        if (timeStamp === this.currentNoteTimeStamp && this.noteDatas.length > 0) {
          this.currentNoteTimeStamp = this.noteDatas[0].timeStamp;
        }
        localStorage.setItem('noteDatas', JSON.stringify(this.noteDatas));
      }
    },
    created() {
      this.theme = localStorage.getItem('noteTheme') || 'light';
      this.noteDatas = JSON.parse(localStorage.getItem('noteDatas')) || noteDatas;
      this.currentNoteTimeStamp = this.noteDatas.length > 0 ? this.noteDatas[0].timeStamp : '';
    }
  });
});
